h1,
.h1 {
  font-size: 36px;
  text-align: center;
  margin: 30px 0;
}

h2,
.h2 {
  font-size: 34px;
  text-align: center;
  margin: 24px 0;
}

h3,
.h3 {
  font-size: 28px;
  text-align: center;
  margin: 18px 0;
}

h4,
.h4 {
  font-size: 20px;
  text-align: center;
  margin: 10px 0;
}

h5,
.h5 {
  font-size: 18px;
  text-align: center;
  margin: 8px 0;
}

h6,
.h6 {
  font-size: 16px;
  text-align: center;
  margin: 6px 0;
}

.up {
  background-color: #7ed321;
  border-radius: 10px;
  color: #000;
  cursor: pointer;
  display: none;
  padding: 1% 2%;
  position: fixed;
  top: 90%;
  left: 87%;
  transition: 1s;
}

.up:hover {
  background-color: #000000;
  color: #ffffff;
}

.head {
  display: flex;
  justify-content: center;
  background: red;
}

.head .header {
  background: white;
}

.main {
  background: #ff0;
}

